@import url('https://fonts.googleapis.com/css2?family=Syne&display=swap');
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Syne';
  
  
  
}



  input[type="text"]
{    background: transparent;
    border: none;
}

input[type="email"]
{    background: transparent;
    border: none;
}

.get-in button{
  background: transparent;
    border: none;
    cursor: pointer;
}



/* Header */
.sm-header{
  display: none;
}
.header{
  display: flex;
  justify-content: space-between;
  padding-top: 0.813rem;
  
  background-color: #1A1A1A;
  color: #626262;
  height: 2.625rem;
}
.sm-sc-header{
  display: none;
}
.left-header{
  display: flex;
  list-style: none;
}
.left-header-height{
  font-size: 1.125rem;
  
}

#margin-right-te{
  margin-left: 5.8rem;
  margin-right: 2rem;}

.right-header{
  display: flex;
  list-style: none;
  margin-right: 5rem;

}

.right-header-height{
  font-size: 0.938rem;


}
.right-margin{
  margin-right: 0.75rem;
}

#right-margin-fourteen{
  margin-right: 0.875rem;
}
#right-margin-thirtyfive{
  margin-right: 2.188rem;
}

.header-img{
  width: 0.5rem;
  height: 0.5rem;
}

/* Header */

/* Lower Header */
.lower-header{
  display: flex;
  justify-content: space-between;
  background: var(--grey-scale-1000, #000);
  color: white;
  align-items: center;
  position: relative;
  
  border-bottom: 1px solid white;
  
  
}


.lower-header h2{
  font-size: 3.75rem;
  font: 'Poppins';
  font-weight: 200;
  margin-left: 8%;
}

.cart{
  display: flex;
  margin-right: 8%;
}

.cart li{
  margin-right: 1.25rem;
  font-weight: 400;
  
}

/* .mobile-menu {
  display: none;
} */

/* Lower Header */


/* Navigation */
.navigation{
  background: var(--grey-scale-1000, #000);
  padding-top: 0.375rem;
  padding-bottom: 8.563rem;
}

.nav-items{
  display: flex;
  font-size: 1.75rem;
  padding-left: 8.563rem;
}
.nav-item{
  color: white;
  list-style: none;
  margin-right: 6.625rem;
  font-weight: 400;
  cursor: pointer;
}

.products{
  border: 1px solid white;
  padding-bottom: 0.25rem;
  
}

.products-border{
  
  border: 1px solid var(--grey-scale-000, #FFF);
  border-bottom: 0px solid var(--grey-scale-000, #FFF);

}

.product{
  list-style: none;
  font-weight: 100;
}


/* Body */

.body{

 
  
  color: white;
  background-color: var(--grey-scale-1000, #000);
  padding-bottom: 3.8rem;
  overflow-x: hidden;
}

.abs-img{
  position: absolute;
  top: 18rem;
  right: 9rem;
  
}

.absc-img{
  display: none;
}

.abs1-img{
  position: absolute;
  top: 17rem;
  right: 10rem;
  
}

.abs2-img{
  display: none;
}

.orange{
  
  height: 22.144rem;
  opacity: 0.8;
  background: var(--pimary-colour, #F07E15);
  margin-left: 31.3rem;
  margin-top: -18rem;
  /* opacity: 0.7; */
  color: #000000;
  font-size: 0.87rem;
  text-align:end;
  /* text-align: end; */
  font-style: normal;
  z-index: 1;
font-weight: 900;
line-height: normal;
text-transform: uppercase;
}

.details-jacket{
  display: grid;
    margin-right: 3rem;
    justify-content: end;
    text-align: left;
}

.see-more{
  border: 1px solid white;
  margin-left: 5%;
  padding: 0.2rem;
}
.line{
  width:33.3%;
  height:3px
}
.new-ele{
  transform: scaleX(2.10);
  transform-origin: 0 0;
}

#fresh{
  font-family: Syne;
  width: 100%;
font-size: 8.438rem;
font-style: normal;
font-weight: 800;
line-height: 6.413rem;
margin-left: 4.2rem;


}

#year{
  font-family: Syne;
  width: 100%;
  /* position: absolute; */
font-size: 8.438rem;
font-style: normal;
font-weight: 800;
line-height: 6.413rem;
color: transparent;
margin-left: 14.14rem;
z-index: 10;
padding-bottom: 1.3rem;
-webkit-text-stroke: 1px var(--grey-scale-1000, rgb(255, 255, 255)); /* Width and color of the outline */
  

}


#look{
  font-family: Syne;
  width: 100%;
font-size: 8.438rem;
font-style: normal;
font-weight: 800;
line-height: 6.413rem;
margin-left: 8.642rem;


}

.body p{
  font-family: Work Sans;
font-size: 1.125rem;
font-style: normal;
font-weight: 400;
line-height: normal;
padding-left: 7.188rem;
}

.body p img{
  padding-left: 0.5rem;
}


/* Products */

.product-header{
  display: flex;
  justify-content: space-around;
  
}

.product-header h2{
  font-size: 5.625rem;
  font-weight: 600;
  color: var(--grey-scale-1000, #000);
  border-bottom: 1px solid  var(--grey-scale-1000, #161615);
  position: relative;
}
.product-header h2::before{
  content: ""; 
  position: absolute;
  left: -4%;
  bottom: -10%; 
  width: 22px; 
  height: 22px; 
  background-image: url('./Components/images/Star\ 2.png'); 
  background-size: cover;
  
}
.arrow{
  font-family: Syne;
  display: flex;
  list-style: none;
  align-items: center;
  
}

.arrow li{
  margin-left: 3rem;
}
.new-products main{
  display: flex;
  
}
.new-products main {
  margin-top: 2.125rem;
}
.new-products main ul{
  list-style: none;
}
.options{
  margin-left: 5.688rem;
}

#accessories{
  font-family: Syne;
  font-size: 26px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  margin-top: 1.438rem;
  margin-bottom: 1.25rem;
}
#best-seller{
  margin-bottom: 1.438rem;

}

.option-list{
  font-family: Work Sans;
font-size: 18px;
font-style: normal;
font-weight: 400;
line-height: normal;
}

.all-products{
  
  position: relative;
  
  overflow: hidden;
  display: flex;
  margin-left: 6.063rem;
  
}

.arrow-2{
  display: none;
}

/* Products */


/* Product Card */

.product-card{
  display: flex;
  flex-direction: column;
  
  margin-left: 1.625rem;
}
.product-details{
  display: flex;
  width: 19.375rem;
  flex-direction: column;
  align-items: flex-start;
  gap:0.5rem;
  padding: 0.5rem;
  
}
#product-img{
  width: 19.375rem;
  height: 20.125rem;
  /* margin-bottom: 0.75rem; */
  flex-shrink: 0;
}

.image-container{
  text-align: end;

}

#product-title{
  font-family: Syne;
  font-size: 1.625rem;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  color: var(--grey-scale-1000, #000);
}


#product-description{
  color: var(--greyscale-400, rgba(22, 22, 21, 0.50));

  font-family: Work Sans;
  font-size: 0.875rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;

}

#pr-price{
  color: var(--grey-scale-1000, #000);

  font-family: Syne;
  font-size: 1.625rem;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

/* Product Card */


/* form */
.form{
  background: url("./Components/images/18591063.png");
  height: 32.5rem;
  color: white;
}

.form-upper{
  display: flex;
  justify-content: space-between;
  

  margin-left: 8rem;
  margin-right: 8rem;
  padding-top: 4rem;
  padding-bottom: 2rem;

}

.new{
  margin-right: 11rem;
 
}

